<template>
    <div class="tmpl">
      <nav-bar title="购物车"></nav-bar>
      <div class="pay-detail">
        <ul>
          <li v-for="(good,index) in goodsList" :key="good.id" class="p-list">
            <mt-switch v-model="good.isSwitch"></mt-switch>
            <img :src="good.face">
            <div class="pay-calc">
              <p>{{good.title}}</p>
              <div class="calc">
                <span>¥{{good.sell_price}}</span>
                <div class="add-del">
                  <span @click="sub(index)">-</span>
                  <span style="width: 50px">{{good.num}}</span>
                  <span @click="add(index)">+</span>
                  <a @click="del(index)">删除</a>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="show-price">
        <div class="show-1">
          <p>总计(不含运费)</p>
          <span>已选择商品{{payment.num}}件，总价¥{{payment.sum}}元</span>
        </div>
        <div class="show-2">
          <mt-button type="danger" size="large">去结算</mt-button>
        </div>
      </div>
    </div>
</template>

<script>
  import prodTools from '../common/prodTools.js'
  import connect from '../common/connect.js'
    export default {
      name: "ShopCart",
      data(){
        return{
          goodsList:[],//购物车商品列表
        }
      },
      created(){
        let prods = prodTools.getProds();
        //判断购物车是否有数据
        if(Object.keys(prods).length == 0) return;
        let url = this.$host+'/api/getshopcartlist/';
        url += Object.keys(prods).join(',');
        this.$axios.get(url)
          .then(res=>{
            console.log(res.data);
            this.goodsList = res.data.data;
            this.goodsList.forEach((ele,index)=>{
              if(prods[ele.id]){
                //动态添加属性
                this.$set(ele,"num",prods[ele.id]);
                this.$set(ele,"isSwitch",true);
              }
            })
          })
          .catch(err=>{
            console.log(err);
          })
      },
      methods:{
        sub(i){
          if(this.goodsList[i].num <= 1) return;
          this.goodsList[i].num--;
          connect.$emit('addShopCart',-1);
          prodTools.addorUpdate({
            id:this.goodsList[i].id,
            num:-1
          })
        },
        add(i){
          if(this.goodsList[i].num >= this.goodsList[i].stock_quantity) return;
          this.goodsList[i].num++;
          connect.$emit('addShopCart',1);
          prodTools.addorUpdate({
            id:this.goodsList[i].id,
            num:1
          })
        },
        //删除购物车
        del(i){
          let goods = this.goodsList[i];
          //删除localStorage
          prodTools.delete(goods.id);
          //变更小球数据
          connect.$emit('addShopCart',-goods.num);
          this.goodsList.splice(i,1);


        }
      },
      computed:{
        payment(){
          let num = 0;//选中商品数量
          let sum = 0;//选中商品总价格
          this.goodsList.forEach((ele)=>{
            //选中则计算
            if(ele.isSwitch){
              num += ele.num;
              sum += ele.num * ele.sell_price;
            };
          });
          return {num,sum};
        }
      }
    }
</script>

<style scoped>
  pay-detail ul li{
    list-style: none;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    margin-bottom: 3px;
  }
  .pay-detail ul{
    padding-left: 5px;
    margin-top: 5px;
  }
  .pay-detail ul li img{
    width: 80px;
    height: 80px;
    display: inline-block;
    vertical-align: top;
    margin-top: 10px;
  }
  .pay-detail ul li >:nth-child(1),
  .pay-detail ul li >:nth-child(3){
    display: inline-block;
  }
  pay-calc p{
    display: inline-block;
    width: 250px;
    overflow: hidden;
    color: blue;
    font-size: 15px;
    margin-bottom: 10px;
  }
  .pay-detail ul li >:nth-child(1){
    line-height: 80px;
  }
  .calc:nth-child(1){
    color: red;
    font-size: 20px;
  }
  .add-del span{
    border: 1px solid rgba(0,0,0,0.3);
    display: inline-block;
    width: 20px;
    text-align: center;
  }
  .calc a{
    margin-left: 20px;
  }
  .show-1,
  .show-2{
    display: inline-block;
    margin-left: 5px;
  }
  .show-2{
    margin-top: 5px;
    margin-right: 5px;
    float: right;
  }
  .show-price{
    background-color: rgba(0,0,0,0.2);
  }
</style>



